@themeColor: #009688;
.progress {
  pointer-events: none;

  .bar {
    background: @themeColor;;
    position: fixed;
    z-index: 1031;
    top: 0;
    left: 0;
    height: 2px;
    transition: width 3s;
  }
}

.peg {
  display: block;
  position: absolute;
  right: 0;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px @themeColor, 0 0 5px @themeColor;
  opacity: 1.0;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
  -ms-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

.spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  left: 15px;
}

.spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;
  border: solid 2px transparent;
  border-top-color: @themeColor;
  border-left-color: @themeColor;
  border-radius: 50%;
  -webkit-animation: progress-spinner 400ms linear infinite;
  animation: progress-spinner 400ms linear infinite;
}

.progress-custom-parent {
  overflow: hidden;
  position: relative;
}

.progress-custom-parent .progress .spinner,
.progress-custom-parent .progress .bar {
  position: absolute;
}

@-webkit-keyframes progress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes progress-spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}