.ya-animation-1 {
  width: 300px;
  animation: text 10s ease infinite;
}
@keyframes text {
  0% {
    color: #7400f9;
  }
  50% {
    color: #00fcbd;
  }
  100% {
    color: #7400f9;
  }
}
.ya-animation-2 {
  position: relative;
  width: 100px;
  height: 100px;
}
.ya-animation-2:after {
  position: absolute;
  content: "";
  top: -5%;
  left: -5%;
  right: 0;
  height: 100%;
  width: 100%;
  transform: scale(1);
  filter: blur(5vw);
  background: linear-gradient(270deg, #00fcbd, #7400f9);
  background-size: 150% 150%;
  border-radius: 50%;
  animation: glowmation 10s linear infinite;
}
@keyframes glowmation {
  0% {
    top: -3%;
    left: -3%;
    background-position: 0% 50%;
    background-size: 150% 150%;
  }
  6% {
    top: -1.5%;
    left: 0;
    background-size: 100% 30%;
  }
  12.5% {
    top: -3%;
    left: 0;
    background-size: 70% 30%;
  }
  16% {
    top: -3%;
    left: 1.5%;
    background-size: 100% 30%;
  }
  25% {
    top: -3%;
    left: 3%;
    background-size: 70% 50%;
  }
  31% {
    top: 0;
    left: 3%;
    background-size: 70% 30%;
  }
  37.5% {
    top: 0;
    left: 3%;
    background-size: 70% 30%;
  }
  50% {
    top: 3%;
    left: 3%;
    background-position: 100% 50%;
    background-size: 30% 30%;
  }
  62.5% {
    top: 3%;
    left: 0;
    background-size: 30% 70%;
  }
  75% {
    top: 3%;
    left: -3%;
    background-size: 50% 100%;
  }
  87.5% {
    top: 0;
    left: -3%;
    background-size: 30% 70%;
  }
  100% {
    top: -3%;
    left: -3%;
    background-position: 0% 50%;
    background-size: 150% 150%;
  }
}
.loader {
  width: 50px;
  height: 50px;
  display: inline-block;
  padding: 0;
  opacity: 0.5;
  border: 3px solid #09acfd;
  animation: loader 1s ease-in-out infinite alternate;
}
.loader:before {
  content: " ";
  position: absolute;
  z-index: -1;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border: 3px solid #09acfd;
}
.loader:after {
  content: " ";
  position: absolute;
  z-index: -1;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border: 3px solid #09acfd;
}
@keyframes loader {
  from {
    transform: rotate(0deg) scale(1, 1);
    border-radius: 0px;
  }
  to {
    transform: rotate(360deg) scale(0, 0);
    border-radius: 50px;
  }
}
.loader1 {
  display: inline-block;
  font-size: 0;
  padding: 0;
}
.loader1 span {
  vertical-align: middle;
