.ya-radio {
  position: relative;
  white-space: nowrap;
  word-break: break-all;
  -webkit-transition: color 0.1s;
  transition: color 0.1s;
  border: 1px solid #ffffff00;
  margin: 0 10px 0 10px;

  .ya-radio-input {
    position: absolute;
    clip: rect(0, 0, 0, 0);

    + .ya-radio-text::before {
      content: "\A0";
      display: inline-block;
      vertical-align: middle;
      width: 16px;
      height: 16px;
      margin-right: 6px;
      margin-left: -11px;
      border-radius: 50%;
      text-indent: 4px;
      margin-bottom: 4px;
      border: 1px solid #CCCCCC;
    }

    &:checked + .ya-radio-text::before {
      background: currentColor;
      background-clip: content-box;
      padding: 2px;
      width: 12px;
      height: 12px;
    }
  }

  .ya-radio-text {
  }
}