/**  */
.ya-richText {
  box-shadow: 0 2px 16px #c3c1c1;
  margin: 15px 15px 0 15px;
  padding: 10px;
  position: fixed;
  bottom: 0;
  right: 0;
  background: white;
}
.ya-richText .ya-richTextBtn {
  height: 25px;
  min-width: 25px;
  padding: 0 5px;
  margin: 1px;
  display: inline-block;
  border: 0;
  background: white;
  font: unset;
  font-size: 14px;
  outline: medium;
}
.ya-richText .ya-richTextBtn:hover {
  border: 1px solid #18ab9c;
  background: #e7fffd;
  box-sizing: border-box;
  margin: 0;
  cursor: pointer;
  animation: all 0.5s;
  border-radius: 5px;
}
.ya-richText .ya-richTextBtn:active {
  background: #c2f7f2;
  border-radius: 5px;
}
.ya-richText .ya-richTextBtn.save {
  color: white;
  background: #ec5356;
  padding: 0 15px;
  margin: 0 5px;
  border-radius: 3px;
  font-size: 14px;
}
.ya-richText .ya-richTextBtn.save:hover {
  border: 0;
  background: #ff3536;
  margin: 0 5px;
  cursor: pointer;
  font: unset;
  font-size: 14px;
}
.ya-richText .ya-richTextBtn.save:active {
  background: #ca0001;
}
.ya-richText .ya-richTextBtn.true {
  display: none;
}
.ya-richText .ya-richTextBtn.minimize {
  float: right;
  display: inline-block;
}
.ya-richText .html {
  font-size: 14px;
  margin: 0 5px;
}
